import Vue from 'vue'
import Vuex from 'vuex'

import config from './config'
import rawData from './rawData'
import race from './race'

Vue.use(Vuex)

const state = {
	...race.state,
	...config.state,
	...rawData.state,
}

const getters = {
	...race.getters,
	...config.getters,
	...rawData.getters,
}

const mutations = {
	...race.mutations,
	...config.mutations,
	...rawData.mutations,
}

const actions = {
	...race.actions,
	...config.actions,
	...rawData.actions,
}

const modules = {}

export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions,
	modules,
})
